/* Mobile View */
@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans&family=Overpass:wght@400;700&display=swap');

body{
    width:100%;
}

*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: 'Kumbh Sans', sans-serif;
}

.lightbox{
  display: none;
}

nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 0.4rem 0rem;

}

ul{
  padding-top: 5rem;
}

.burger-bar{
  position:relative;
  width:20px;
  height:4px;
  background:black;
  border-radius: 20px;
  transition: all 0.2s ease-in-out;
}

.burger-bar::after,
.burger-bar::before{
  content: "";
  position:absolute;
  width:20px;
  height:4px;
  background:black;
  border-radius: 20px;
  transition: all 0.2s ease-in-out;
}

.burger-bar::before{
  transform: translateY(-7px);
}

.burger-bar::after{
  transform: translateY(7px);
}

.burger-menu{
  position: relative;
  display:flex;
  justify-content: center;
  align-items: center;
  width:30px;
  height:30px;
  cursor:pointer;
  transition: all 3s ease-in-out;
  z-index: 5;
  /* margin-left: 1.5rem; */
}

/* animation */
.burger-menu.open .burger-bar{
  transform: translateX(-40px);
  background: transparent;
}

.burger-menu.open .burger-bar::before{
  transform: rotate(45deg) translate(28px, -28px);
  background-color: hsl(219, 9%, 45%);
}

.burger-menu.open .burger-bar::after{
  transform: rotate(-45deg) translate(28px, 28px);
  background-color: hsl(219, 9%, 45%);
}

.navbarimg-div{
    display: flex;
    min-width:0.3rem;
    justify-content: space-around;
    align-items: center;
    margin-right:1rem;
}

.prof-img{
  max-width:35%;
}

.content-div{
  display: flex;
  flex-direction: column;
}

.product-img{
  max-width:100%;
}

.text-div{
  padding:1.4rem;
}
  
h3{
  letter-spacing: 0.05rem;
  font-size: 0.8rem;
  margin-bottom: 1rem;
  color:hsl(26, 100%, 50%);
}

h1{
  font-size:1.9rem;
  font-weight: bolder;
}

.text-div > h1{
  margin-bottom: 1.3rem;
  line-height: 2rem;
}

.text-div > p{
  letter-spacing: 0.03rem;
  line-height:1.5rem ;
  margin-bottom: 2rem;
}

p{
  font-size: 1rem;
  font-weight: bold;
  color: hsl(220, 14%, 75%);
}

.price-container{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1.5rem;
}

.price{
  display:inline;
  letter-spacing: 0.1rem;
}

.percentage-btn{
  background-color: hsl(25, 100%, 94%);
  color: hsl(26, 100%, 55%);
  font-weight: 700;
  letter-spacing: 0.06rem;
  padding:0.313rem 0.5rem;
  border-radius: 0.313rem;
  border:none;
  font-size: 1rem;
  margin-left:1.25rem;
}

.carousel-img-container{
  display: none;
}

.canc-text{
  text-decoration: line-through;
  margin-left: auto;
}

.multi-btn{
  height:2.5rem;
  padding:2% 0;
  border-radius: 0.56rem;
  width:8.125rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(220, 14%, 95%);
  margin-bottom: 1rem;;
}

.cart-btn{
  border:none;
  background-color: hsl(26, 100%, 55%);
  height:1.25rem;
  width: 19rem;
  padding:6% 0;
  border-radius: 0.56rem;
  display: flex;
  align-items: center;
  color:white;
  font-weight: 700;
  justify-content: center;
  letter-spacing: 0.06rem;
}

.plus-img{
  width: 0.75rem;
  height:0.75rem;
  background-color: hsl(220, 14%, 95%);
  margin-right:1.5rem;
}

.btn-div{
  display:flex;
  flex-direction: column;
  padding:3% 0;
  align-items: center;
}

.btn-div > * {
  width:99%;
  padding:1.5rem 0;
}

.plus-img:active, 
.minus-img:active, 
.cart-btn:active, 
.checkout-btn:active{
  opacity:0.5;
}

.minus-img{
  width: 0.75rem;
  height:0.25rem;
  background-color: hsl(220, 14%, 95%);
  margin-left:1.5rem;
} 

.change-image-div{
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width:100%;
  top:45%;
  margin: auto;
}

.change-image-div > div{
  background-color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.9rem 1rem;
}

.prev-div{
  margin-left: 0.8em;
}

.next-div{
  margin-right: 0.8em;;
}

.nav-links{
  display: flex;
  position:absolute;
  margin-left:0;                                                                                                                                                                                                                                                                
  left:0px;
  height:100vh;
  top:0;
  display: flex;
  flex-direction: column;
  align-items: left;
  background-color: white;
  width:63%;
  font-weight: bolder;
  display:none;
  z-index: 3;
}

.changeDisplay{
  display:block;
}

.nav-links li{
  padding: 1rem 0rem;
  margin-left: 1.5rem;
  list-style-type: none;
  font-size: 1.3rem;
  color:hsl(219, 9%, 45%);
  cursor: pointer;
}

.close-btn{
  display:block;
  width:1rem;
  margin-top:1.5rem;
  margin-left: 1.5rem;
  margin-bottom:1.7rem;
}

.menu-btn{
  padding: 2rem 0rem;;
}

.logo-menu-div{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width:60%;
  margin-left:0rem;
}

.img-div{
  position: relative;
}

/* Cart */
.cart-popup{
  position:absolute;
  border-radius:.5rem;
  width:95%;
  max-width:380px;
  height:15rem;
  object-position: 50%;
  margin-left: auto;
  margin-right: auto;
  left:0;
  right:0;
  top:0.5rem;
  background-color: white;
  box-shadow:0.0rem 0.6rem 1.1rem #888888;
  display:flex;
  flex-flow: column;
  margin: 0 auto;
  transform:  scale(0.1);
  visibility: hidden;
  transition: transform 0.3s, top 0.3s;
  z-index: 4;
}

.open-popup{
  visibility: visible;
  transform:  scale(1);
}

.img-des-amt{
  display: flex;
  justify-content: space-around;
}

.cart-header{
  flex: 0 1 auto;
}

.cart-body{
  display: flex;
  justify-content:center;
  align-items:center;
  flex:1 0 auto;
}

.cart-popup > hr{
  align-self: center;
  width:90%;
  opacity:0.2;
}

.cart-header > h2{
  padding:1rem;
  font-size:1rem;
}

.fill-cart{
  width:100%;
}

.fill-cart>div{
  display:flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 0.5rem;
}

.check-img{
  width:3rem;
  height:3rem;
  border-radius: 0.2rem;
  margin-left: 1rem;
}

.trashcan-img{
  margin-right:1rem;
  width:1.5rem;
}
.check-out-text{
  padding: 0 1rem;
}

.checkout-btn{
  height:3rem;
  width: 100%;
  background-color: hsl(26, 100%, 55%);
  border:none;
  border-radius: 0.56rem;
  color: white;
  font-weight: 700;
  letter-spacing: 0.1rem;
}

.fin{
  font-weight: bolder;
  color:black;
}

.popup{
  background-color: hsl(26, 100%, 55%);
  color:white;
  padding:0 0.6rem;
  position:absolute;
  border-radius: 1.5rem;
  margin-right:3rem;
  margin-bottom: 1.5rem;
}

@media screen and (min-width:900px) {
  nav{
    justify-content: space-evenly; 
   }

   body{
     display: flex;
     justify-content: center;
   }

   .lightbox{
     position:fixed;
     z-index: 99;
     height: 100%;
     width: 100%;
     background-color: rgba(0,0,0,0.8);
     display: none;
     justify-content: center;
     align-items: center;
   }

   .lightbox .close-btn{
    width:2rem;
    height: 2rem;
    margin-left: 26rem;
   }

   .lightbox .close-btn :hover{
    fill:orangered;
   }

   .lightbox img{
    width:450px;
    height: 450px;
    border-radius: 10px;
    cursor: pointer;
   }

   
   .lightbox div > img{
    width:5.5rem;
    height: 5.5rem;
    margin-top:2rem;
    margin-left: 1rem;
    margin-right: 1rem;
    border-radius: 5px;
   }
 
   .active{
    display: block;
    display:flex;
    flex-direction: column;
   }

   .burger-menu{
     display: none;
   }
 
   h3{
     color:hsl(26, 100%, 50%);
     opacity: 0.5;
    font-size: .7rem;
   }
 
   ul{
     padding-top: 0;
   }
 
   h1{
     font-size: 1.7rem;
   }

   p{
    font-size: .8rem;
    font-weight: bold;
    color: hsl(220, 14%, 75%);
  }
 
   .product-img{
     border-radius: 1rem;
     min-width: 250px;
   }

   .carousel-img{
      margin-top:0.4rem;
      width:5.5rem;
      height:auto;
      border-radius: 0.7rem;
    }

    .carousel-img:hover{
      opacity: 0.5;
    }

    .carousel-img:active{
      border: solid hsl(26, 100%, 55%);
      opacity:0.5;
    }

    .carousel-img-container{
      display: flex;
      justify-content: space-between;
    }
 
   .nav-links{
     display: flex;
     position:inherit;
     margin-left:1.5rem;                                                                                                                                                                                                                                                                
     left:0;
     height:0;
     top:0;
     display: flex;
     flex-direction: row;
     align-items: center;
     background-color: transparent;
     width:100%;
     font-weight: normal;
     z-index: 0;
   }
 
   .logo-menu-div{
     padding: 3rem 0rem;
   }
 
   .prof-img{
     max-width:40%;
   }
 
   .navbarimg-div{
     justify-content: space-around;
   }
 
   .nav-links li{
     /* padding: 0 0rem; */
     /* margin-left: 0rem; */
     font-size: 0.8rem;
     color:hsl(219, 9%, 75%);
   }
 
   .content-div{
     flex-direction: row;
     justify-content: space-between;
     padding-top: 1.5rem;
     margin-right: 6.5rem;
     margin-left: 6.5rem;
   }
 
   .img-div{
     margin-right:1rem;
   }
 
   .text-div{
     padding-top: 3rem;
     margin-left: 1rem;
   }
 
   .text-div > p{
    margin-bottom:1rem;
  }

  .text-div > h1{
    margin-bottom: 1rem;
    line-height: 2rem;
    
  }

   .change-image-div{
     display: none;
   }
 
   .open-popup{
     transform:  scale(0.1);
     transform:  translate(180%, -40%) scale(1);
     transition: transform 0.3s, top 0.3s;
   }
 
   .btn-div{
     flex-direction: row;
     justify-content: space-between;
     
   }
 
   .cart-btn{
     height:1.25rem;
     width: 60%;
   }
 
   .multi-btn{
     height: 1.25rem;
     width:35%;
     margin-bottom: 0;
     font-size: 1.3rem;
   }
 
   .price{
     font-size:1.2rem;
   }

   .price-container{
    justify-content: start;
    align-items: flex-start;
    flex-direction: column;

  }

  .pcont{
    display: flex;
    margin-bottom: 0.8rem;
  }

  .canc-text{
    justify-self: start;
    margin-left: 0;
  }

   .minus-img{
     margin-left:0.8rem;
   } 
 
   .plus-img{
     margin-right: 0.8rem;
   }

   .percentage-btn{
     font-size: 0.6rem;
   }
}

@media screen and (min-width: 1130px){

  h1{
    font-size: 2.5rem;
  }

  .product-img{
    min-width: 400px;
  }

  .nav-links{
    margin-left:2.5rem;                                                                                                                                                                                                                                                                
  }

  .nav-links li{
    padding: 0 0.5rem;
    margin-left: 1rem;
    font-size: 1rem;
  }

  .content-div{
    margin-right: 10rem;
    margin-left: 10rem;
    padding-top: 3.5rem;
  }

  .text-div{
    padding-top: 5rem;
  }

  .btn-div{
    margin-top: 3rem;
  }

  .text-div > p{
    margin-bottom:2rem;
  }

  .text-div > h1{
    margin-bottom: 1.3rem;
    line-height: 99%;
  }

  .cart-btn{
    height:1.25rem;
    width: 60%;
  }

  .multi-btn{
    height: 1.25rem;
    width:35%;
    margin-bottom: 0;
    font-size: 1.3rem;
  }

  .price{
    font-size:1.5rem;
  }

  .percentage-btn{
    font-size: 0.8rem;
  }
  
}

